'use client';

import { Search, X, Filter } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";

interface JenisTransaksiFiltersProps {
    searchQuery: string;
    setSearchQuery: (value: string) => void;
    filterTipe: string;
    setFilterTipe: (value: string) => void;
}

export default function JenisTransaksiFilters({
    searchQuery,
    setSearchQuery,
    filterTipe,
    setFilterTipe
}: JenisTransaksiFiltersProps) {
    return (
        <div className="flex flex-col md:flex-row gap-4 p-4 border border-gray-200 rounded-lg shadow-sm bg-white">
            <div className="relative flex-1">
                <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                <Input
                    placeholder="Cari nama jenis transaksi..."
                    className="pl-9"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
            </div>
            
            <div className="w-full md:w-[200px]">
                 <Select value={filterTipe} onValueChange={setFilterTipe}>
                    <SelectTrigger>
                        <Filter className="w-4 h-4 mr-2 text-gray-500" />
                        <SelectValue placeholder="Filter Tipe" />
                    </SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">Semua Tipe</SelectItem>
                        <SelectItem value="masuk">Pemasukan (Kredit)</SelectItem>
                        <SelectItem value="keluar">Pengeluaran (Debit)</SelectItem>
                    </SelectContent>
                </Select>
            </div>

            {(searchQuery || filterTipe !== 'all') && (
                <Button variant="ghost" onClick={() => { setSearchQuery(''); setFilterTipe('all'); }} className="text-gray-500">
                    <X className="h-4 w-4 mr-2" /> Reset
                </Button>
            )}
        </div>
    );
}