'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { JenisTransaksi } from '@/types';

// Import Komponen
import JenisTransaksiFilters from '@/components/admin/jenis-transaksi/JenisTransaksiFilters';
import JenisTransaksiTable from '@/components/admin/jenis-transaksi/JenisTransaksiTable';
import JenisTransaksiFormDialog from '@/components/admin/jenis-transaksi/JenisTransaksiFormDialog';

export default function JenisTransaksiPage() {
    const [data, setData] = useState<JenisTransaksi[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<JenisTransaksi | null>(null);

    // Filter
    const [searchQuery, setSearchQuery] = useState('');
    const [filterTipe, setFilterTipe] = useState('all');
    
    // Pagination
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // Fetch
    const fetchData = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/jenisTransaksis');
            setData(response.data);
        } catch (error) {
            toast.error("Gagal memuat data.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchData(); }, []);

    // Logic Filter
    const filteredData = data.filter((item) => {
        const matchSearch = item.nama_jenis.toLowerCase().includes(searchQuery.toLowerCase());
        const matchTipe = filterTipe === 'all' || item.tipe === filterTipe;
        return matchSearch && matchTipe;
    });

    // Logic Pagination
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredData.slice(indexOfFirstItem, indexOfLastItem);

    // Handlers
    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: JenisTransaksi) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus data ini?")) return;
        try {
            await axios.delete(`/api/jenisTransaksis/${id}`);
            toast.success("Berhasil dihapus.");
            fetchData();
        } catch (error: any) {
            toast.error("Gagal menghapus (Mungkin sedang digunakan).");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                await axios.put(`/api/jenisTransaksis/${editData.id}`, formData);
                toast.success("Berhasil diperbarui!");
            } else {
                await axios.post('/api/jenisTransaksis', formData);
                toast.success("Berhasil ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchData();
        } catch (error: any) {
            toast.error("Gagal menyimpan data.");
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Jenis Transaksi (COA)</h1>
                    <p className="text-gray-500 text-sm">Kelola kategori pemasukan dan pengeluaran keuangan.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Jenis
                </Button>
            </div>

            <JenisTransaksiFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterTipe={filterTipe}
                setFilterTipe={setFilterTipe}
            />

            <JenisTransaksiTable 
                loading={loading}
                data={currentItems}
                totalData={filteredData.length}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <JenisTransaksiFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
            />
        </div>
    );
}