'use client';

import { useState, useEffect, FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from "@/components/ui/select";
import {
    Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogDescription,
} from '@/components/ui/dialog';
import { JenisTransaksi } from '@/types';

interface JenisTransaksiFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (formData: any, isEdit: boolean) => Promise<void>;
    initialData: JenisTransaksi | null;
}

export default function JenisTransaksiFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    initialData,
}: JenisTransaksiFormDialogProps) {
    const [isSaving, setIsSaving] = useState(false);
    
    const [formData, setFormData] = useState({
        nama_jenis: '',
        tipe: 'masuk',
        deskripsi: '',
    });

    useEffect(() => {
        if (isOpen && initialData) {
            setFormData({
                nama_jenis: initialData.nama_jenis,
                tipe: initialData.tipe,
                deskripsi: initialData.deskripsi || '',
            });
        } else if (isOpen && !initialData) {
            setFormData({
                nama_jenis: '',
                tipe: 'masuk',
                deskripsi: '',
            });
        }
    }, [isOpen, initialData]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);
        await onSubmit(formData, !!initialData);
        setIsSaving(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Jenis Transaksi' : 'Tambah Jenis Baru'}</DialogTitle>
                    <DialogDescription>Kategorikan transaksi untuk laporan keuangan yang lebih rapi.</DialogDescription>
                </DialogHeader>

                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="space-y-2">
                        <Label>Nama Jenis <span className="text-red-500">*</span></Label>
                        <Input 
                            value={formData.nama_jenis} 
                            onChange={e => setFormData({...formData, nama_jenis: e.target.value})} 
                            required 
                            placeholder="Cth: Gaji Karyawan, Listrik, Bonus" 
                        />
                    </div>

                    <div className="space-y-2">
                        <Label>Tipe Transaksi <span className="text-red-500">*</span></Label>
                        <Select value={formData.tipe} onValueChange={(v) => setFormData({...formData, tipe: v})}>
                            <SelectTrigger>
                                <SelectValue placeholder="Pilih Tipe" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="masuk">Pemasukan (Income)</SelectItem>
                                <SelectItem value="keluar">Pengeluaran (Expense)</SelectItem>
                            </SelectContent>
                        </Select>
                        <p className="text-xs text-gray-500">
                            {formData.tipe === 'masuk' 
                                ? 'Akan menambah saldo kas (Kredit).' 
                                : 'Akan mengurangi saldo kas (Debit).'}
                        </p>
                    </div>

                    <div className="space-y-2">
                        <Label>Deskripsi</Label>
                        <Textarea 
                            value={formData.deskripsi} 
                            onChange={e => setFormData({...formData, deskripsi: e.target.value})} 
                            placeholder="Penjelasan singkat..." 
                        />
                    </div>

                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving ? <Loader2 className="h-4 w-4 animate-spin" /> : 'Simpan'}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}