'use client';

import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, ArrowDownCircle, ArrowUpCircle } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { JenisTransaksi } from '@/types';

interface JenisTransaksiTableProps {
    loading: boolean;
    data: JenisTransaksi[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: JenisTransaksi) => void;
    onDelete: (id: number) => void;
}

export default function JenisTransaksiTable({
    loading,
    data,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: JenisTransaksiTableProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const startNumber = (currentPage - 1) * itemsPerPage + 1;

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead>Nama Jenis</TableHead>
                        <TableHead>Tipe</TableHead>
                        <TableHead>Deskripsi</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={5} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : data.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={5} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Data tidak ditemukan.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        data.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50">
                                <TableCell className="text-center text-gray-500">
                                    {startNumber + index}
                                </TableCell>
                                <TableCell className="font-medium text-gray-900">
                                    {item.nama_jenis}
                                </TableCell>
                                <TableCell>
                                    {item.tipe === 'masuk' ? (
                                        <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200 shadow-none">
                                            <ArrowUpCircle className="w-3 h-3 mr-1" /> Pemasukan
                                        </Badge>
                                    ) : (
                                        <Badge className="bg-red-100 text-red-800 hover:bg-red-100 border-red-200 shadow-none">
                                            <ArrowDownCircle className="w-3 h-3 mr-1" /> Pengeluaran
                                        </Badge>
                                    )}
                                </TableCell>
                                <TableCell className="text-gray-500 text-sm truncate max-w-[200px]">
                                    {item.deskripsi || '-'}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    <Button variant="ghost" size="icon" onClick={() => onEdit(item)} className="hover:bg-blue-50 hover:text-blue-600">
                                        <Pencil className="h-4 w-4" />
                                    </Button>
                                    {/* Proteksi ID bawaan sistem jika perlu, misal ID 1-5 jangan dihapus */}
                                    <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)} className="hover:bg-red-50 hover:text-red-600">
                                        <Trash2 className="h-4 w-4" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>
            
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">Total: {totalData} Data</div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages}>
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}